import { getCategoryUnitList } from '@/api/goodsApi.js'
import T from '@/utils/tips.js'
export default {
	data() {
		return {
			hasData:true,
			unit: '',
			unitId:'',
			stock:'',
			is70: true,
			unitList:[],
			unitLists:[],
			skuAttrValues:[
				{
					name: '',
					value:''
				}
			]
		};
	},
	components: {},
	onLoad() {
		
	},
	onShow() {
		// 获取分类单位
		this.getCategoryUnitList()
	},
	methods:{
		checkName(){
			this.assessHasData()
		},
		checkValue(){
			this.assessHasData()
		},
		// 库存输入框
		checkStock(){
			this.assessHasData()
		},
		// 判断数据是否完整
		assessHasData(){
			this.hasData =  this.unit == '' || this.stock == '' || this.skuAttrValues[0].name =='' || this.skuAttrValues[0].value ==''
			
		},
		// 获取分类单位
		getCategoryUnitList(){
			let data = {
				categoryId : JSON.parse(uni.getStorageSync('varieties')).id ,
				status: 1
			}
			getCategoryUnitList(data).then(res=>{
				if(res.code == '1000'){
					this.unitLists = res.data
					this.unitList = []
					res.data.forEach(item=>{
						this.unitList.push(item.name)
					})
					 
				}
			})
		},
		getUnit(){
			let _this = this
			uni.showActionSheet({
			    itemList: this.unitList,
			    success: function (res) {
					_this.unit = _this.unitList[res.tapIndex]
					_this.unitId = _this.unitLists[res.tapIndex].id
			        console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					// 判断数据是否完整
					_this.assessHasData()
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		},
		// 新增报价
		add(){
			let obj = {
				name: '',
				value:''
			}
			this.skuAttrValues.push(obj);
		},
		// 删除报价
		del(index){
			if(index > 0){
				this.skuAttrValues.splice(index,1)
			}
			// 判断数据是否完整
			this.assessHasData()
		},
		// 保存
		save(){
			if(this.unit == '') {
				T.tips('请选择计量单位')
				return false
			}
			if(this.stock == '') {
				T.tips('请填写库存')
				return false
			}
			if(this.skuAttrValues[0].name == '' && this.skuAttrValues[0].value == '' ) {
				T.tips('至少填写一个起批量和价格')
				return false
			}
			
			
			
			
			
		}
	}
}
